.heading-intro {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.7;
  white-space: normal;
  word-wrap: break-word;
}
.heading-intro p {
  margin: 1em auto;
}
.heading-intro ul li {
  margin: 0 0 5px;
  padding: 0 0 0 16px;
  position: relative;
}
.heading-intro ul li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 12px;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background: #333333;
}
.inner-page .wrapped-section {
  width: auto;
  margin-left: auto;
  margin-right: auto;
  max-width: 980px;
  padding-left: 20px;
  padding-right: 20px;
}
.inner-page .section-description {
  font-size:18px;
  font-family:"Open Sans", sans-serif;
  color:#333;
  line-height:1.7;
  padding:0 30px;
  width: auto;
  max-width: 980px;
  margin-left: auto;
  margin-right: auto;
}
.centered-section {
  text-align: center;
}

.section-description.no-careers {
  font-size: 22px;
  font-weight: 500;
}
