.careers-page .intro {
  margin: 0 auto;
  width: auto;
}
.inner-page .form-description {
  width: auto;
  max-width: 650px;
  margin: auto;
  font-size:18px;
  font-family:"Open Sans", sans-serif;
  color:#333;
  line-height:1.7;
  padding:0 30px;
}
.careers-list {
  width: auto;
  max-width: 980px;
  padding: 0 20px;
  margin: 50px auto;
  text-align: center;
}
.careers-list__item {
  width: calc( 50% - 8px);
  margin: 4px;
  background: #fbfbfb;
  position: relative;
}
.careers-item__link {
  text-decoration: none;
  position: relative;
  display: block;
  padding-bottom: 120px;
  height: calc( 100% - 120px );
}
.careers-item__heading {
  display: block;
  height: 148px;
  background-size: cover;
  background-position: center;
}
.careers-item__image {
  margin: -70px auto 18px;
}
.careers-list__item h2 {
  color: #555;
  font-family: "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 400;
  margin: 0 48px;
}
.careers-requirements__wrapper {
  padding: 30px 70px;
  text-align: left;
  box-sizing: border-box;
}
.careers-requirements__wrapper p,
.careers-requirements__wrapper ul li {
  margin:0 0 5px;
  line-height:1.4;
  font-size:16px;
  padding:0 0 0 25px;
  position:relative;
}

.careers-requirements__wrapper p {
  padding: 0;
}

.careers-requirements__wrapper p+ul {
  margin-top: 20px;
}

.careers-requirements__wrapper ul li ul {
  margin: 15px 0 15px 20px;
}
.single-career-page .careers-requirements__wrapper p:not(:last-of-type) {
  margin-bottom: 25px;
}
.page-careers .careers-requirements__wrapper {
  font-size:16px;
}
.careers-requirements__wrapper ul li p {
    padding: 0;
}
.careers-requirements__wrapper ul li:before {
  content:"";
  position:absolute;
  left:0;
  top:8px;
  width:7px;
  height:7px;
  border-radius: 100%;
  background:#f1563c;
}

.subscribe-job-wrapper {
  font-size: 1.333em;
  padding: 0 20px;
}
.subscribe-job-wrapper a {
  font-size: inherit;
}
