.office-pin-wrapper {
  position: absolute;
  top: 40.5%;
  margin-top: -1.42em;
  z-index: 3;
  text-decoration: none;
  color: #333333;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  padding: 0 0 0 1.25em;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  /*transition: all 0.2s ease;*/
  /*-webkit-transition: all 0.2s ease;*/
}
.no-touch  .office-pin-wrapper:hover {
  text-decoration: none;
  user-select: none;
  transition: text-shadow 0.2s ease;
  -webkit-transition: text-shadow 0.2s ease;
}
.office-pin-wrapper:hover .office-city {
 color: #f1563c;
}
.no-touch .office-pin-wrapper:hover .office-pin {
  fill: #f1563c;
  transform: translateY(0px);
  -webkit-transform: translateY(0px);
  animation: float 1s ease-in-out infinite;
}
.no-touch .office-pin-wrapper:hover .office-pin path {
  fill: #f1563c;
}
.office-pin-wrapper.disabled {
  color: #333333;
}

.office-pin-wrapper.disabled .office-city {
  color: #8C8C8C;
}
.office-pin-wrapper.disabled .office-pin {
  fill: #8C8C8C;
}
.no-touch .office-pin-wrapper.disabled:hover .office-pin {
  animation: none 0s;
  fill: #8C8C8C;
}
.office-pin-wrapper:active .office-pin  {
  fill: #f1563c;
}
@keyframes float {
  0% {
    /*box-shadow: 0px 19px 11px -7px rgba(0,0,0,0.3);*/
    transform: translatey(0px);
  }
  50% {
    /*box-shadow: 0px 29px 11px -8px rgba(0,0,0,0.2);*/
    transform: translatey(-10px);
  }
  100% {
    /*box-shadow: 0px 19px 11px -7px rgba(0,0,0,0.3);*/
    transform: translatey(0px);
  }
}
